* {
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
  font-family: "Ubuntu", sans-serif;
}
body {
  background: white;
}
h1,
h4,
h3,
a {
  color: black;
}
ul {
  list-style: none;
}
.row {
  text-align: center;
}

.Body {
  margin-top: 30px;
  padding: 10px 30px;
}
.Body h4 {
  color: #05386b;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 40px;
}
.hr {
  width: 10%;
  height: 5px;
  color: black;
  background-color: black;
  margin: auto;
  margin-top: 50px;
  border-radius: 20px;
  border: 1px solid black;
}
.text {
  margin-bottom: 30px;
}
.Main {
  justify-content: space-between;
  padding-left: 50px;
  padding-right: 50px;
}
.Col-1,
.Col-2 {
  background: rgba(255, 255, 255, 0.7);
  padding: 50px;
  border: 1px solid #05386b;
  border-radius: 20px;
}
.Col-2 p {
  color: black;
  font-weight: 450;
  padding-top: 50px;
  padding-bottom: 50px;
}
.Col-1 p {
  color: black;
  font-weight: 450;
  padding-top: 50px;
  padding-bottom: 50px;
}
.l-250 {
  display: none;
}
.container-1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.ret {
  width: 50%;
  float: left;
}
.text-1 {
  width: 50%;
  float: left;
}
.ret img {
  max-width: 500px;
  max-height: 200px;
}
.text-1 {
  display: flex;
  flex-direction: column;
}
.text-1 h4 {
  float: left;
  font-size: 1.4rem;
}
.inner p {
  display: inline-block;
  text-align: left;
  color: black;
  font-size: 0.9rem;
  font-weight: 420;
}

