.button {
  margin-top: 30px;
  margin-bottom: 30px;
  color: white;
  padding: 10px 20px;
  background-color: #5dbea3;
  border-radius: 40px;
  border: 1px solid #5dbea3;
  font-weight: 600;
}
.button:hover {
  color: #5dbea3;
  background: transparent;
}
.form {
  width: 100%;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 30px;
  text-align: center;
}
.form form {
  margin-top: 40px;
  margin-bottom: 20px;
}
.form h4 {
  padding-top: 70px;
  color: #d14836;
  font-weight: 700;
}
.form-text {
  background-color: whitesmoke;
  border: 2px solid rgba(220, 220, 220, 0.7);
  border-radius: 5px;
}
.form p {
  color: black;
  font-weight: 600;
}
.awards p {
  color: black;
  font-weight: 700;
  font-size: 1.2rem;
}

@media screen and (max-width: 840px) {
  .a-1 img {
    height: 150px;
    width: 150px;
  }
  .a-1 {
    padding: 30px 20px;
  }
  .Col-1 img {
    height: 270px;
  }
  .Col-2 img {
    height: 270px;
  }
  .Col-1 {
    margin-bottom: 30px;
  }
  .Main {
    padding-left: 2.5px;
    padding-right: 2.5px;
  }
  .inner h4 {
    font-size: 1.1rem;
  }
}
@media screen and (max-width: 300px) {
  .container-1 {
    width: 100%;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin-right: 25px;
  }
  .l-250 {
    display: inline-block;
  }
  .l-250 p {
    padding-top: 20px;
    text-align: center;
    color: black;
    font-size: 0.9rem;
    font-weight: 450;
  }
  .inner-p {
    display: none;
  }
  .inner h4 {
    float: left;
  }
}
@media screen and (max-width: 1024px) {
  .ret img {
    height: 150px;
    width: 150px;
  }
}

#success {
  display: none;
  margin-left: 30px;
  margin-right: 30px;
  align-items: center;
}
#success p {
  color: hsl(120, 90%, 44%);
  font-weight: 500;
}
#danger {
  display: none;
  margin-left: 30px;
  margin-right: 30px;
  align-items: center;
}
#danger p {
  color: red;
  font-weight: 500;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  top: 0;
  position: fixed;
  z-index: 20;
  background: black;
  width: 100%;
  box-shadow: 0px 2px rgba(233, 233, 233);
}
.nav-menu {
  display: flex;
}
.nav-menu li {
  padding: 0 1rem;
  position: relative;
}
.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
  text-decoration: none;
}
.nav-menu li a:hover {
  color: #d14836;
}
.nav-menu li a::after {
  content: "";
  position: absolute;
  background-color: #d14836;
  height: 2px;
  width: 0;
  left: 50%;
  bottom: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) all;
}

.nav-menu li a:hover {
  color: #d14836;
}
.nav-menu li a:hover::after {
  width: 60%;
}
.icon {
  display: none;
}

@media screen and (max-width: 768px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60vh;
    background: rgba(255, 255, 255, 1);
    position: absolute;
    top: 80px;
    left: -100%;
    z-index: 10;
    transition: 0.5s;
  }
  .nav-menu.active {
    left: 0;
  }
  .nav-menu li {
    padding: 1rem 0.7rem 1rem 0;
  }
  .nav-menu li a {
    font-size: 1.4rem;
    color: black;
  }
  .icon {
    display: initial;
    transition: 0, 5s ease-in-out;
  }
}
@media screen and (max-width: 300px) {
  .nav-menu li a {
    font-size: 1rem;
  }
}

.row-f {
  text-align: center;
}
@media screen and (max-width: 768px) {
  .empty {
    display: none;
  }
}
.row-f a {
  text-decoration: none;
}
.footer {
  background-color: black;
  padding: 30px;
}
.logo {
  margin-bottom: 10px;
}
.copy {
  width: 100%;
  padding: 0;
  margin: 0;
}
.footer p {
  color: white;
}
.copy p {
  font-size: 0.8rem;
  color: white;
}
.illu p {
  font-size: smaller;
  color: rgba(120, 120, 120);
}
.illu a {
  color: rgba(120, 120, 120);
}

.slider {
  margin-top: 100px;
}
.slider img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.text-img {
  background-color: rgba(0, 0, 0, 0.4);
}
.text-img h3 {
  color: white;
}
.slider h4 {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #05386b;
}
.hr {
  width: 10%;
  height: 5px;
  color: black;
  background-color: black;
  margin: auto;
  margin-top: 50px;
  border-radius: 20px;
  border: 1px solid black;
}
@media screen and (min-width: 1000px) {
  .slider img {
    height: 70vh;
    padding: 10px 20px;
  }
}
@media screen and (max-width: 1000px) {
  .slider img {
    height: 40vh;
  }
}
@media screen and (min-width: 800px) and (max-width: 1000px) {
  .slider {
    margin-top: 150px;
  }
}

